import {Component, OnDestroy, OnInit} from '@angular/core';

@Component({
  selector: 'app-slide-show',
  templateUrl: './slide-show.component.html',
  styleUrls: ['./slide-show.component.css']
})
export class SlideShowComponent implements OnInit, OnDestroy {

  public cArr;
  public index;
  public timer;

  constructor() { }

  ngOnInit() {
    this.cArr = [ 'p3', 'p2', 'p1'];
    this.index = 0;

    this.timer = setInterval(() => { this.nextimg(); }, 4000);
  }

  public previmg() {
    this.cArr.unshift( this.cArr[2] );
    this.cArr.pop();
    const liArr = document.getElementsByTagName('li');
    for (let i = 0 ; i < liArr.length; i++) {
      liArr[i].className = this.cArr[i];
    }
    this.index--;
    if (this.index < 0) {
      this.index = 2;
    }
}

// 下一张
  public nextimg() {
    this.cArr.push(this.cArr[0]);
    this.cArr.shift();
    const liArr = document.getElementsByTagName('li');
    for (let i = 0 ; i < liArr.length; i++) {
      liArr[i].className = this.cArr[i];
    }
    this.index++;
    if (this.index > 2) {
      this.index = 0;
    }
  }

  public onBox() {
    clearInterval(this.timer);
  }
  public outBox() {
    this.timer = setInterval(() => { this.nextimg(); }, 4000);
  }

  public onPic(target) {
    if (target.className === 'p1') {
      this.previmg();
      return false; // 返回一个false值，让a标签不跳转
    }else if (target.className === 'p3') {
      this.nextimg();
      return false;
    }else {
      return true;
    }
  }
  ngOnDestroy() {
    clearInterval(this.timer);
  }

}



